.App {
  text-align: center;
}
@import url("https://fonts.googleapis.com/css?family=Waiting+for+the+Sunrise&family=Didact+Gothic");

* {
  scroll-behavior: smooth;
}

body {
  font-family: "Didact Gothic";
  font-weight: 300;
  font-size: 16px;
  margin: 0;
  padding: 0;
  width: 100%;
}

h1,
h2,
h3,
h4,
h5 {
  font-family: "Waiting for the Sunrise";
}

a {
  text-decoration: none;
  color: #6be405;
  font-weight: bold;
}

img {
  max-width: 100%;
}

nav {
  width: 100%;
  background: #108db8;
}

nav a {
  color: white;
  padding: 1rem;
  display: inline-block;
}

.content {
  padding: 1rem;
}
